import React, { useState } from "react";
import classes from "../../Users/Users.module.css";
import cn from "classnames";
import Button from "@material-ui/core/Button";
import SkipNextSharpIcon from "@material-ui/icons/SkipNextSharp";
import SkipPreviousSharpIcon from "@material-ui/icons/SkipPreviousSharp";


let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {
  let pagesCount = Math.ceil(totalItemsCount / pageSize);

  let pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }



  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionPageNumber = portionNumber * portionSize;

 

  return (
    <div className={classes.paginatorCL}>
      {portionNumber > 1 && (
        <Button
          size="small"
          color="secondary"
          onClick={() => {
            setPortionNumber(portionNumber - 1);
          }}
        > <SkipPreviousSharpIcon /> </Button>
      )}
      {pages
        .filter(
          (p) => p >= leftPortionPageNumber && p <= rightPortionPageNumber
        )
        .map((p) => {
          return (
            <span
              className={cn({ [classes.selectedPage]: currentPage === p},
                classes.pageNumber )}
              key={p}
              onClick={(e) => { 
                onPageChanged(p);
              }}
            > {p} </span>
          );
        })}{portionCount > portionNumber && (
        <Button
          size="small"
          color="secondary"
          onClick={() => {
            setPortionNumber(portionNumber + 1);
          }} > <SkipNextSharpIcon /> </Button> )}
    </div>
  );
};

export default Paginator;
