import React, { useEffect } from 'react';
import { reduxForm } from 'redux-form';
import {
  InputJsx,
  createField,
  CheckboxForm,
} from "../common/FormsControls/FormsControls";
import { required } from '../../validators';
import { connect } from 'react-redux';
import { login, logOut } from './../../redux/auth-reducer'
import { Redirect } from 'react-router-dom';
import styles from './../../components/common/FormsControls/FormsControls.module.css';
import { Button } from '@material-ui/core';
import classes from './Login.module.css'
import stayHome2 from '../../assets/images/stayHome2.jpg'


const LoginForm = ({ handleSubmit, error, captchaUrl }) => {
  return <>
      <form className={classes.form} onSubmit={handleSubmit}>
        <div>{createField("email", "email", InputJsx, [required])}</div>
        <div>
          {createField("Password", "password", InputJsx, [required], {
            type: "password",
          })}
        </div>
        <div>
          {createField(null, "rememberMe", CheckboxForm, [])}
          remember me
        </div>

        {captchaUrl ? <img src={captchaUrl} alt="" /> : null}
        {captchaUrl ?  createField("Symbols", "captcha", InputJsx, [required]) : null}

        {error ? <div className={styles.formSummaryError}>
            {error}
          </div> : null}
        <div className={classes.btn}>
          <Button size="small" variant="contained" color="primary" type="submit">
            Login
          </Button>
        </div>
      </form>
    </>;
};


const LoginReduxForm = reduxForm({
  form: "login"
})(LoginForm);


const Login = (props) => {
  const onSubmit = formData => {
    props.login(formData.email, formData.password, formData.rememberMe, formData.captcha);
  };

if (props.isAuth) {
  return <Redirect to={"/profile"} />
}

  return <div className={classes.loginAndimg}>
      <div>
        <h1>Login</h1>
        <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl} />
      </div>
      <div>
        <img src={stayHome2} alt="" className={classes.stayHome} />
      </div>
    </div>;
};

const mapStateToProps = (state) => ({
  captchaUrl: state.auth.captchaUrl,
  isAuth: state.auth.isAuth,
})

export default connect(mapStateToProps, { login, logOut })(Login);