const SEND_MESSAGE = "SEND_MESSAGE";

const initialState = {
  messageData: [
    { id: 1, message: "kak ty" },
    { id: 2, message: "pridesh segodnya" },
    { id: 3, message: "gde ty shlyaeshsya" },
    { id: 4, message: "gde ty shlyaeshsya" },
    { id: 5, message: "pridesh segodnya" }
  ],
  dialogsData: [
    { id: 1, name: "Thomas Shelby" },
    { id: 2, name: "Tony Stark" },
    { id: 3, name: "Sergo" },
    { id: 4, name: "John Shelby" },
    { id: 5, name: "Billie Eilish" },
    { id: 6, name: "Kaxa" },
    { id: 7, name: "SomeBody" }
  ],
};

const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case SEND_MESSAGE: 
        const body = action.newMessageBody;
        return {
          ...state,
          messageData: [...state.messageData, { id: 6, message: body }]
        };
    default:
      return state
  }
};

export const sendMessageCreator = newMessageBody => ({
         type: SEND_MESSAGE,
         newMessageBody
       });



export default dialogsReducer;