import { profileApi } from "../api/api";
import { stopSubmit } from "redux-form";

const ADD_POST = "ADD-POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_STATUS = "SET_STATUS";
const DELETE_POST = "DELETE_POST";
const SAVE_PHOTO_SUCCESS = "SAVE_PHOTO_SUCCESS";

const initialState = {
  postData: [
    { id: 1, message: "Hi, how are you", like: "15 likes" },
    { id: 2, message: "It's my first post", like: "16 likes" }
  ],
  profile: null,
  status: "",

};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST:
      const newPost = { id: 3, message: action.AddNewPost, like: "13 likes" };
      return { ...state, newPostText: "", postData: [...state.postData, newPost] };
    case SET_USER_PROFILE: {
      return { ...state, profile: action.profile };
    }
    case SET_STATUS:
      return { ...state, status: action.status };
    case DELETE_POST:
      return { ...state, ...state.postData.filter(p => p.id !== action.postId) };
    case SAVE_PHOTO_SUCCESS:
      debugger;
      return { ...state, profile: { ...state.profile, photos: action.photos } };
    default:
      return state;
  }
};


export const addPostActionCreator = AddNewPost => ({
         type: ADD_POST,
         AddNewPost
       });
export const setUserProfile = profile => ({ type: SET_USER_PROFILE, profile });
export const setStatus = status => ({ type: SET_STATUS, status });
export const deletePost = postId => ({ type: DELETE_POST, postId });
export const savePhotoSuccess = photos => ({
  type: SAVE_PHOTO_SUCCESS,
  payload: photos
});


export const getUserProfile = userId => async dispatch => {
  let data = await profileApi.getProfile(userId)
    dispatch(setUserProfile(data));
};

export const getStatus = userId => async dispatch => {
  let data = await profileApi.getStatus(userId)
    dispatch(setStatus(data));
};

export const updateStatus = status => async dispatch => {
  let data = await profileApi.updateStatus(status)
    if (data.resultCode === 0) {
      dispatch(setStatus(status));
    }
};

export const savePhoto = file => async dispatch => {
         let data = await profileApi.savePhoto(file);
         if (data.resultCode === 0) {
           dispatch(savePhotoSuccess(data.data.photos));
         }
       };

export const saveProfile = profile => async (dispatch, getState) => {
         const userId = getState().auth.userId;
         let response = await profileApi.saveProfile(profile);
         if (response.data.resultCode === 0) {
           dispatch(getUserProfile(userId));
         } else {
           dispatch(stopSubmit("edit-profile", {
               _error: response.data.messages[0],
             }));
               return Promise.reject(response.data.messages[0]);
         }
       };


export default profileReducer;